<template>
    <div>
        <block class="adv-g__coupon-info__main">
            <DiscountItemList
                    v-bind:offer="coupon"
            ></DiscountItemList>
        </block>
        <details-section>
            <InfoTable
                    v-bind:discount-info-list="coupon.discountInfoList">
            </InfoTable>
        </details-section>
        <delimiter></delimiter>
        <template v-if="config.isMapActive">
            <details-section>
                <OpenMap
                        v-on:openMap="$emit('openMap')"
                        v-bind:map-string="coupon.mapString"
                ></OpenMap>
            </details-section>
            <delimiter></delimiter>
        </template>
        <details-section>
            <heading size="h5">{{coupon.usageTitleLabel}}</heading>
            <textblock
                    size="small"
                    class="adv-g__coupon-info__info-part__value adv-g__coupon-info__usage-text"
            ><span v-html="usageLabel"></span>
            </textblock>
        </details-section>
        <delimiter></delimiter>
        <details-section>
            <phone-section
                    v-bind:info-label="coupon.infoLabel"
                    v-bind:phone-label="coupon.phoneLabel"
            ></phone-section>
        </details-section>
        <SelfHealing
                v-if="coupon.isMed"
                class="adv-g__coupon-info__self-healing">
            >
        </SelfHealing>
    </div>
</template>

<script>
    import Block from "../../ui/Block";
    import DetailsSection from "./common/DetailsSection";
    import Heading from "../../ui/Heading";
    import Textblock from "../../ui/TextBlock";
    import Delimiter from "../../ui/Delimiter";
    import OpenMap from "./common/OpenMap";
    import PhoneSection from "./common/PhoneSection";
    import SelfHealing from "./common/SelfHealing";
    import InfoTable from "./common/InfoTable";
    import DiscountItemList from "./common/DiscountItemList";
    import {GalleryConfigAwareMixin} from '../../../mixins/GalleryConfigAware';


    export default {
        name: "CouponInformation",
        mixins: [GalleryConfigAwareMixin],
        components: {
            DiscountItemList,
            InfoTable,
            SelfHealing,
            PhoneSection,
            OpenMap,
            Delimiter,
            Textblock,
            Heading,
            DetailsSection,
            Block
        },
        props: {
            coupon: {
                type: Object
            }
        },
        computed: {
            usageLabel() {
                return this.coupon.usageLabel.replace(/\r\n/g, '<br>');
            }
        }
    }
</script>

<style scoped>
    .adv-g__coupon-info__info-part__value {
        font-weight: 500;
    }

    .adv-g__coupon-info__usage-text {
        text-align: left;
    }

    .adv-g__coupon-info__self-healing {
        width: 481px;
        height: 92px;
        margin-left: -15px;
        margin-bottom: 20px;
    }
</style>
