import * as chroma from 'chroma-js';

export const GalleryConfigMixin = {
    data() {
        return {
            config: {
                isSearchActive: false,
                isFiltersActive: true,
                isMapActive: false,
                isLoginActive: false,
                isBasketActive: false,
                isBasketActiveForNonLoginUsers: false,
                colors:  {
                    primaryColor: 'rgb(11, 177, 67)',
                    darkerPrimaryColor: chroma('rgb(11, 177, 67)').darken(1.2).hex(),
                    primaryTextColor: 'black',
                    secondaryTextColor: chroma('black').brighten(3).hex(),
                    secondaryBorderColor: chroma('black').brighten(4).hex()
                }
            },
        };
    },
    computed: {
        isHeaderShown() {
            const {isSearchActive, isFiltersActive, isLoginActive, isBasketActive, isBasketActiveForNonLoginUsers} = this.config;

            return isSearchActive
                || isFiltersActive
                || isLoginActive
                || isBasketActive
                || isBasketActiveForNonLoginUsers;
        },
        isBasketShown() {
            const {isBasketActive, isBasketActiveForNonLoginUsers} = this.config;

            return isBasketActive && (isBasketActiveForNonLoginUsers || this.userIsLoggedIn);
        }
    },
    methods: {
        setGalleryConfig({
                             isSearchActive,
                             isFiltersActive,
                             isMapActive,
                             isLoginActive,
                             isBasketActive,
                             isBasketActiveForNonLoginUsers,
                             primaryColor,
                             primaryTextColor
                         }) {
            this.config.isSearchActive = isSearchActive;
            this.config.isFiltersActive = isFiltersActive;
            this.config.isMapActive = isMapActive;
            this.config.isLoginActive = isLoginActive;
            this.config.isBasketActive = isBasketActive;
            this.config.isBasketActiveForNonLoginUsers = isBasketActiveForNonLoginUsers;

            const textColor = primaryTextColor === '#FFFFFF' ? 'black' : primaryTextColor;
            this.config.colors = {
                primaryColor,
                darkerPrimaryColor: chroma(primaryColor).darken(1.2).hex(),
                primaryTextColor: textColor,
                secondaryTextColor: chroma(textColor).brighten(3).hex(),
                secondaryBorderColor: chroma(textColor).brighten(4.5).hex()
            };
        }
    }

}
