export const sortOffers = (sorting) => {
    switch (sorting) {
        case 'range':
            return (a, b) => sortByPosition(a, b);
        case 'alphabet':
            return (a, b) => sortByAlphabet(a, b);
        case 'date':
            return (a, b) => sortByDate(a, b);
        case 'discount':
            return (a, b) => sortByDiscount(a, b);
    }
};

function sortByAlphabet(couponA, couponB) {
    return (couponA.title || couponA.promotionTitle)
        .localeCompare(couponB.title || couponB.promotionTitle);
}

function sortByDate(couponA, couponB) {
    return new Date(couponA.endDateTime).getTime() - new Date(couponB.endDateTime).getTime();
}

function sortByDiscount(couponA, couponB) {
    return couponA.discountValue - couponB.discountValue;
}

function sortByPosition(couponA, couponB) {
    return couponA.position - couponB.position;
}