<template>
    <a class="adv-g__phone-link" v-bind:href="phoneHref">{{phone}}</a>
</template>

<script>
    export default {
        name: "PhoneLink",
        props: {
            phone: {
                type: String,
                required: true
            }
        },
        computed: {
            phoneHref() {
                return `tel:${this.phone}`;
            }
        }
    }
</script>

<style scoped>
    .adv-g__phone-link {
        color: inherit;
        font-size: inherit;
        font-weight: inherit;
        text-decoration: inherit;
    }
</style>