<template>
    <span class="adv-g__text"
          v-bind:class="{
              'adv-g__text_small': size === 'small',
              'adv-g__text_extra-small': size === 'extra-small'
          }"
    >
        <slot></slot>
    </span>
</template>

<script>
    export default {
        name: "textblock",
        props: {
            size: {
                type: String,
                validator(value) {
                    return !value || ['extra-small', 'small'].includes(value);
                }
            }
        }
    }
</script>

<style scoped>
    .adv-g__text {
        font-size: 1rem; /*16px*/
        line-height: 1.5rem;
    }

    .adv-g__text_small {
        font-size: 0.875rem; /*14px*/
        line-height: 1.32rem;
    }

    .adv-g__text_extra-small {
        font-size: 0.75rem; /*12px*/
        line-height: 1.125rem;
    }
</style>