export function downloadFile(url, body = null) {
    if (!body && checkIOS()) {
        window.open(url, '_blank');
        return Promise.resolve();
    }

    return makeRequest(url, body)
        .then(xhr => {
            createAndDownloadBlobFile(xhr.response, getFileName(xhr, 'Offer'));
        });
}

function getFileName(request, defaultName) {
    const disposition = request.getResponseHeader('Content-Disposition');
    const matches = /filename=(".*")/.exec(disposition);
    return (matches != null && matches[1] ? matches[1] : defaultName);
}

function makeRequest(url, body = null) {
    return new Promise((resolve, reject) => {
        const xhr = new XMLHttpRequest();
        xhr.open(body ? 'POST' : 'GET', url, true);
        xhr.responseType = 'blob';

        xhr.onreadystatechange = () => {
            if (xhr.readyState !== XMLHttpRequest.DONE) {
                return;
            }
            if (xhr.status != 200) {
                reject();
                //TODO: add processing of errors
            } else {
                resolve(xhr);
            }
        };

        if (body) {
            xhr.setRequestHeader("Content-Type", "application/json");
            xhr.send(JSON.stringify(body));
        } else {
            xhr.send();
        }
    });
}

function createAndDownloadBlobFile(body, filename, extension = 'pdf') {
    const blob = new Blob([body], {type: 'application/pdf'});
    const fileName = `${filename}.${extension}`;
    if (navigator.msSaveBlob) {
        // IE 10+
        navigator.msSaveBlob(blob, fileName);
    } else {
        const link = document.createElement('a');
        // Browsers that support HTML5 download attribute
        if (link.download !== undefined) {
            const url = URL.createObjectURL(blob);
            link.setAttribute('href', url);
            link.setAttribute('download', fileName);
            link.style.visibility = 'hidden';
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
        }
    }
}

export function checkIOS() {
    return navigator.userAgent.match(/iPhone|iPad|iPod/i);
}
