<template>
    <div class="adv-g__cart"
         role="button"
         v-bind:class="{'adv-g__cart_filled': cart.length}"
         v-on:click="toggleDropdown"
    >
        <CartSvg class="adv-g__cart__icon"></CartSvg>
        <div class="adv-g__cart__selected">
            <div class="adv-g__cart__selected__label">{{i18n.cart.selected}}</div>
            <div class="adv-g__cart__selected__amount">{{cart.length}} / {{totalCouponsNumber}}</div>
        </div>
        <Btn v-bind:type="cart.length ? 'primary' : 'secondary'"
             class="adv-g__cart__download-btn"
             v-on:click.stop="downloadCartPdf"
        >
            <template v-slot:icon>
                <DownloadSvg
                        v-if="!loading"
                        class="adv-btn__icon"
                ></DownloadSvg>
                <Spinner
                        v-if="loading"
                        class="adv-btn__icon"
                ></Spinner>
            </template>
        </Btn>
        <cart-list
                v-if="dropdownOpen"
                v-bind:coupons="cart"
                v-on:download="downloadCartPdf"
                v-on:remove="onRemoveCoupon"
                v-bind:loading-pdf="loading"
        ></cart-list>
    </div>
</template>

<script>
    import CartList from "./CartList";
    import CartSvg from '../../../assets/shopping-basket.svg';
    import DownloadSvg from '../../../assets/download.svg';
    import Btn from '../../ui/Button';
    import { downloadFile } from "../../../utils/download-file";
    import * as http from './../../../utils/http';
    import Spinner from '../../../assets/anitmated-spinner.svg';
    import { i18nAwareMixin } from "../../../mixins/i18n/I18nAware";

    export default {
        name: "cart",
        components: {Btn, DownloadSvg, CartSvg, CartList, Spinner},
        mixins: [i18nAwareMixin],
        data() {
            return {
                dropdownOpen: false,
                loading: false
            };
        },
        props: {
            cart: {
                type: Array,
                default() {
                    return [];
                }
            },
            totalCouponsNumber: {
                type: Number,
                default: 0
            }
        },
        methods: {
            toggleDropdown() {
                if (!this.cart.length) {
                    return;
                }

                this.dropdownOpen = !this.dropdownOpen;
            },
            downloadCartPdf() {
                if (!this.cart.length) {
                    return;
                }

                if (this.loading) {
                    return;
                }

                this.loading = true;

                const body = this.cart.map(item => ({
                    OfferId: item.offerId,
                    NetworkIds: item.networkIds
                }));

                http.post(
                    'https://digitalsavertest.azurewebsites.net/api/digitalOffers/GenerateCart',
                    body
                )
                    .then(url => downloadFile(url))
                    .then(() => this.loading = false)
                    .catch(() => this.loading = false)
            },
            onRemoveCoupon(coupon) {
                this.$emit('remove', coupon);
            }
        },
        watch: {
            cart() {
                if (this.cart.length) {
                    return;
                }

                this.dropdownOpen = false;
            }
        }
    }
</script>

<style>
    .adv-g__cart {
        position: relative;
        border-radius: 4px;
        border: 1px solid var(--adv-gallery-secondary-border-color);
        max-width: 260px;
        flex-grow: 1;
        height: 64px;
        display: flex;
        align-items: center;
        padding-left: 20px;
        background: white;
    }

    .adv-g__cart_filled {
        border: 1px solid var(--adv-gallery-main-color);
        cursor: pointer;
    }

    .adv-g__cart__icon {
        width: 40px;
        height: 40px;
        fill: var(--adv-gallery-secondary-text-color);
    }

    .adv-g__cart_filled .adv-g__cart__icon {
        fill: var(--adv-gallery-main-color);
    }

    .adv-g__cart__selected {
        padding-left: 20px;
        padding-right: 20px;
        font-size: 0.9rem;
        line-height: 1.4;
        user-select: none;
    }

    .adv-g__cart__selected__label {
        color: var(--adv-gallery-secondary-text-color);
    }

    .adv-g__cart__selected__amount {
        font-weight: bold;
    }

    .adv-g__cart__download-btn {
        height: 100%;
        width: 72px;
        margin-left: auto;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
        cursor: default;
    }

    .adv-g__cart_filled .adv-g__cart__download-btn {
        cursor: pointer;
    }

    @media screen and (max-width: 400px) {
        .adv-g__cart {
            padding-left: 10px;
        }

        .adv-g__cart__icon {
            width: 35px;
            height: 35px;
        }

        .adv-g__cart__selected {
            padding-left: 10px;
            padding-right: 10px;
        }
    }

    @media screen and (max-width: 380px) {
        .adv-g__cart__download-btn {
            display: none;
        }
    }
</style>
