<template>
    <div class="adv-coupon"
         v-bind:class="{
            'adv-coupon_promocode': isPromocode,
            'adv-coupon_promotion': isPromotion,
            'adv-coupon_coupon': isCoupon
         }"
    >
        <svg class="adv-coupon__coupon-border"
             v-if="isCoupon"
             viewBox='0 0 270 361'
             preserveAspectRatio='none'>
            <path d='M0,0 270,0 270,361 0,361z' />
        </svg>
        <div class="adv-coupon__body">
            <div class="adv-coupon__img-wrapper">
                <img
                        v-bind:src="offer.imageLink"
                        class="adv-coupon__img">
            </div>
            <div class="adv-coupon__discount">
                {{offer.mainDiscountString}}
            </div>
            <div class="adv-coupon__discount-for-header">
                {{offer.discountForString || offer.promotionTitle}}
            </div>
            <div class="adv-coupon__product-title" v-bind:title="title">
                {{title}}
            </div>
            <div>
                <span class="adv-coupon__valid-till-message">{{offer.actionTimeString}}
                </span>&#32;<span class="adv-coupon__valid-till-date">{{formattedDate}}</span>
            </div>
            <Btn
                    type="secondary_outline"
                    size="sm"
                    class="adv-coupon__info-btn"
                    v-on:click="openInfo">
                <template v-slot:icon>
                    <InfoSvg class="adv-btn__icon"></InfoSvg>
                </template>
            </Btn>
        </div>
        <div class="adv-coupon__actions">
            <div class="adv-coupon__actions__left">
                <DownloadOfferPDFBtn
                        v-bind:offer="offer"
                ></DownloadOfferPDFBtn>
            </div>
            <div class="adv-coupon__actions__right">
                <Btn
                        v-if="isBasketShown"
                        v-bind:type="selected ? 'primary_outline' : 'secondary_outline'"
                        v-on:click="toggleSelection"
                >
                    <template v-slot:icon>
                        <CartSvg class="adv-btn__icon"></CartSvg>
                    </template>
                </Btn>
                <ShareBtn v-bind:offer="offer"></ShareBtn>
            </div>
        </div>
    </div>
</template>

<script>
    import InfoSvg from '../../assets/baseline-info-24px.svg';
    import CartSvg from '../../assets/cart.svg';
    import Btn from '../ui/Button';
    import { GalleryConfigAwareMixin } from '../../mixins/GalleryConfigAware';
    import ShareBtn from "./share/ShareBtn";
    import DownloadOfferPDFBtn from "./download/DownloadOfferPDFBtn";

    export default {
        name: "coupon",
        mixins: [GalleryConfigAwareMixin],
        components: {
            DownloadOfferPDFBtn,
            ShareBtn,
            InfoSvg,
            CartSvg,
            Btn
        },
        props: {
            offer: {
                type: Object,
            },
            selected: {
                type: Boolean,
                default: false,
            }
        },
        computed: {
            coupon() {
                return this.offer;
            },
            formattedDate() {
                return new Date(this.offer.endDateTime).toLocaleDateString();
            },
            title() {
                return this.offer.title || this.offer.promotionMainText;
            },
            offerType() {
                return this.offer.offerType;
            },
            isPromotion() {
                return this.offerType === 'PROMOTION';
            },
            isCoupon() {
                return this.offerType === 'COUPON';
            },
            isPromocode() {
                return this.offerType === 'PROMOCODE';
            }
        },
        methods: {
            toggleSelection() {
                this.$emit('selection', this.offer);
            },
            openInfo() {
                this.$emit('info', this.offer);
            },
        }
    }
</script>

