        border-radius: 5px;
        pointer-events: none;
    }

    .adv-coupon__coupon-border > path {
        fill: none;
        stroke: var(--adv-gallery-main-color);
        stroke-width: 3;
        stroke-dasharray: 12, 6;
    }

    .adv-coupon__body {
        height: 300px;
        width: 100%;
        padding: 20px 20px 0 20px;
        position: relative;
    }

    .adv-coupon__actions {
        width: 100%;
        padding: 0 20px 20px 20px;
        display: flex;
        justify-content: space-between;
    }

    .adv-coupon__actions__left {
        display: flex;
        justify-content: space-between;
    }

    .adv-coupon__actions__right {
        display: flex;
        justify-content: flex-end;
    }

    .adv-coupon__actions__right > * {
        margin-left: 8px;
    }

    .adv-coupon__img-wrapper {
        overflow: hidden;
        position: relative;
        height: 135px;
    }

    .adv-coupon__img {
        width: 100%;
        min-width: 100px;
        left: 50%;
        margin-left: -50%;
        top: 50%;
        margin-top: -50%;
        position: absolute;
    }

    .adv-coupon__discount {
        font-weight: bold;
        font-size: 1.6rem;
        line-height: 2.4rem;
        padding-top: 15px;
        height: calc(2.4rem + 15px);
    }

    .adv-coupon__discount-for-header {
        font-size: 0.85rem;
        color: var(--adv-gallery-secondary-text-color);;
        line-height: 1.3rem;
        height: 1.3rem;
    }

    .adv-coupon__product-title {
        font-weight: bold;
        overflow: hidden;
        height: 2.4rem;
        font-size: 0.9rem;
        line-height: 1.2rem;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    .adv-coupon__valid-till-message {
        font-size: 0.85rem;
