export function initGoogleSdk(googleClientID) {
    return new Promise(resolve => {
        window.advGalleryGoogleAuthInit = () => {
            resolve();
            delete window.advGalleryGoogleAuthInit;
        };

        addScript('https://apis.google.com/js/platform.js?onload=advGalleryGoogleAuthInit');
        addScript('https://apis.google.com/js/api:client.js');
    })
        .then(() => loadsAuth2())
        .then(() => initAuth(googleClientID))
        .then(auth => {
            if (auth.isSignedIn.get()) {
                return auth.currentUser.get();
            } else {
                return false;
            }
        });

}

function loadsAuth2() {
    return new Promise(resolve => {
        window.gapi.load('auth2', resolve);
    });
}


function addScript(url) {
    let googleAuthScript = document.createElement('script')
    googleAuthScript.setAttribute('src', url);
    document.head.appendChild(googleAuthScript);
}

function initAuth(googleClientID) {
    const auth2 = window.gapi.auth2;

    return auth2.init({client_id: googleClientID});
}

export function logoutWithGoogle() {
    return window.gapi.auth2
        .getAuthInstance()
        .signOut();
}