<template>
    <label>
        <textblock
                class="adv-g__input-label"
                v-if="$slots.default"
                size="small">
            <slot></slot>
        </textblock>
        <input
                class="adv-g__input"
                type="text"
                v-bind:value="value"
                v-bind:placeholder="placeholder"
                v-on:input="$emit('input', $event.target.value)"
                v-on:focus="$emit('focus', $event)"
                v-on:blur="$emit('blur', $event)"
        >
    </label>
</template>

<script>
    import Textblock from "./TextBlock";
    export default {
        name: "TextInput",
        components: {Textblock},
        props: {
            value: {
                type: String
            },
            placeholder: {
                type: String,
                default: ''
            }
        },
    }
</script>

