import * as http from '../../../utils/http';

export function createShortShareURL(url) {
    return http.post('https://firebasedynamiclinks.googleapis.com/v1/shortLinks', {
        "dynamicLinkInfo": {
            dynamicLinkDomain: 'advcoupon.page.link',
            link: url
        },
        "suffix": {
            "option": "SHORT"
        }
    }, undefined, { key: 'AIzaSyDRLVPp_zrKCAt4waswrbVqpNJK9D02-Ak' })
        .then(res => JSON.parse(res))
        .then(res => res.shortLink)
        .catch(() => url);
}
