export function get(url, queryParams = null) {
    return new Promise((resolve, reject) => {
        const xhr = new XMLHttpRequest();
        xhr.open('GET', url + (queryParams ? `?${serialize(queryParams)}` : ''), true);
        xhr.send();

        xhr.onreadystatechange = () => {
            if (xhr.readyState !== XMLHttpRequest.DONE) {
                return;
            }
            if (xhr.status != 200) {
                reject();
                //TODO: add processing of errors
            } else {
                resolve(JSON.parse(xhr.responseText));
            }
        }
    });
}

function serialize(obj) {
    var str = [];
    for (var p in obj)
        if (obj.hasOwnProperty(p)) {
            str.push(encodeURIComponent(p) + "=" + encodeURIComponent(obj[p]));
        }
    return str.join("&");
}

export function post(url, body, requestHeaders = {"Content-Type": "application/json"}, queryParams = undefined) {
    return new Promise((resolve, reject) => {
        const xhr = new XMLHttpRequest();
        xhr.open('POST', url + (queryParams ? `?${serialize(queryParams)}` : ''), true);

        for (const header in requestHeaders) {
            xhr.setRequestHeader(header, requestHeaders[header]);
        }

        xhr.onreadystatechange = () => {
            if (xhr.readyState !== XMLHttpRequest.DONE) {
                return;
            }
            if (xhr.status != 200) {
                reject();
                //TODO: add processing of errors
            } else {
                resolve(xhr.responseText);
            }
        }

        xhr.send(JSON.stringify(body));
    });
}
