<template>
    <button
            class="adv-btn"
            v-bind:class="btnCls"
            v-on:click="onClick"
            type="button"
    >
        <slot name="icon"></slot>
        <span class="adv-btn__name"><slot name="name"></slot></span>
    </button>
</template>

<script>
    export default {
        name: "btn",
        props: {
            type: {
                type: String,
                default: 'primary',
                validator(value) {
                    return !![
                        'primary',
                        'secondary',
                        'primary_outline',
                        'secondary_outline',
                        'link',
                        'danger_outline'
                    ].includes(value);
                }
            },
            size: {
                type: String,
                default: 'md',
                validator(value) {
                    return !!['sm', 'md', 'lg'].includes(value);
                }
            }
        },
        computed: {
            btnCls() {
                return [
                    'adv-btn_' + this.type,
                    {
                        'adv-btn_small': this.size === 'sm',
                        'adv-btn_large': this.size === 'lg',
                        'adv-btn_medium': this.size === 'md',
                        'adv-btn_icon': !this.$slots['name'],
                        'adv-btn_name': !this.$slots['icon'],
                    }
                ];
            }
        },
        methods: {
            onClick($event) {
                this.$emit('click', $event);
            }
        }
    }
</script>

<style>
    .adv-btn {
        display: inline-block;
        text-align: center;
        user-select: none;
        background-color: transparent;
        font-size: 0.8rem;
        line-height: 0.8rem;
        padding: 5px 12px;
        border-radius: 3px;
        cursor: pointer;
        min-height: 2.4rem;
        border: 1px solid grey;
        white-space: nowrap;
    }

    .adv-btn * {
        vertical-align: middle;
    }

    .adv-btn .adv-btn__icon {
        width: 22px;
        height: 22px;
    }

    .adv-btn.adv-btn_small {
        min-height: 2rem;
    }

    .adv-btn.adv-btn_small .adv-btn__icon {
        width: 18px;
        height: 18px;
    }

    .adv-btn__name {
        margin-left: 5px;
    }

    .adv-btn.adv-btn_icon {
        padding: 5px 7px;
    }

    .adv-btn_icon .adv-btn__name {
        display: none;
    }

    .adv-btn_name .adv-btn__name {
        margin-left: 0;
    }

    .adv-btn_small {
        border-radius: 2px;
        padding: 5px 5px;
    }

    .adv-btn_medium {
        font-size: 1rem;
        line-height: 1.5rem;
    }

    .adv-btn_large {
        font-size: 1.4rem;
        line-height: 2.1rem;
    }

    .adv-btn_primary {
        color: white;
        background: var(--adv-gallery-main-color);
        border: var(--adv-gallery-main-color) 1px solid;
    }

    .adv-btn_primary_outline {
        color: var(--adv-gallery-main-color);
        border: var(--adv-gallery-main-color) 1px solid;
        background: white;
    }

    .adv-btn_link {
        border: none;
        background: transparent;
        color: var(--adv-gallery-main-color);
    }

    .adv-btn_primary .adv-btn__icon,
    .adv-btn_secondary .adv-btn__icon {
        fill: white;
    }

    .adv-btn_primary_outline .adv-btn__icon {
        fill: var(--adv-gallery-main-color);
    }

    .adv-btn_link .adv-btn__icon {
        fill: var(--adv-gallery-main-color);
    }

    .adv-btn_secondary {
        color: white;
        background: var(--adv-gallery-secondary-text-color);
        border: 1px solid var(--adv-gallery-secondary-border-color);
    }

    .adv-btn_secondary_outline {
        color: var(--adv-gallery-secondary-text-color);
        border-color: var(--adv-gallery-secondary-border-color);
        background: white;
    }

    .adv-btn_secondary_outline .adv-btn__icon {
        fill: var(--adv-gallery-secondary-text-color);
    }

    .adv-btn_danger_outline {
        color: red;
        background: white;
        border: 1px solid rgba(255, 0, 0, 0.22);
    }

    .adv-btn_danger_outline .adv-btn__icon {
        fill: red;
    }

    .adv-btn_secondary_outline .adv-btn__icon *:not([fill=none]) {
        fill: var(--adv-gallery-secondary-text-color);
        border: var(--adv-gallery-secondary-border-color) 1px solid;
        border-radius: 3px;
    }
</style>
