<template>
    <div class="adv-gallery" v-bind:style="cssVars">
        <template v-if="!loadingData">
            <Header v-if="isHeaderShown">
                <template v-slot:left>
                    <search-input
                            v-if="config.isSearchActive"
                            v-model="filterInput"
                    ></search-input>
                    <filter-button
                            v-if="config.isFiltersActive"
                            v-on:click="filterOpen = true"
                            v-bind:filter-applied="filterApplied"
                    ></filter-button>
                </template>
                <template v-slot:right>
                    <cart
                            v-if="isBasketShown"
                            v-bind:cart="selectedCoupons"
                            v-bind:total-coupons-number="coupons.length"
                            v-on:remove="toggleCouponInCart"
                    ></cart>
                    <login
                            v-if="config.isLoginActive"
                            class="adv-g__header_login"
                    ></login>
                </template>
            </Header>
            <div class="adv-g__coupons-list">
                <coupon
                        v-for="(offer, index) in filteredBySearch"
                        v-bind:offer="offer"
                        v-bind:key="offer.id"
                        v-bind:selected="cart[offer.id]"
                        v-on:info="openInfo"
                        v-on:selection="toggleCouponInCart"
                        v-bind:style="{'z-index': filteredBySearch.length - index}"
                >
                </coupon>
            </div>
            <details-information
                    v-if="openedCouponInfo"
                    v-bind:offer="openedCouponInfo"
                    v-on:openMap="openMap"
                    v-on:close="closeInfo">
            </details-information>
            <map-modal
                    v-if="isCouponMapOpened && openedCouponInfo"
                    v-bind:offer="openedCouponInfo"
                    v-bind:filterData="filterData"
                    v-on:close="closeMap"
            ></map-modal>
            <filter-modal
                    v-if="filterOpen && config.isFiltersActive"
                    v-bind:filterData="filterData"
                    v-on:filterApplied="filterOpen = false"
                    v-on:close="filterOpen = false"
                    v-model="filterState"
            ></filter-modal>
        </template>
        <Spinner
                class="adv-g__spinner"
                v-if="loadingData"
        ></Spinner>
    </div>
</template>

<script>
    import Coupon from './components/business-logic/Coupon';
    import DetailsInformation from "./components/business-logic/details/DetailsInformation";
    import Header from "./components/ui/Header";
    import Cart from "./components/business-logic/cart/Cart";
    import Vue from "vue";
    import * as http from './utils/http';
    import Login from "./components/business-logic/login/Login";
    import MapModal from "./components/business-logic/maps/MapModal";
    import FilterModal from "./components/business-logic/filter/Filter";
    import FilterButton from "./components/business-logic/filter/FilterButton";
    import SearchInput from "./components/business-logic/filter/SearchInput";
    import { GalleryConfigMixin } from './mixins/GalleryConfig';
    import { sortOffers } from "./utils/sorting/sort-offers";
    import { i18nMixin } from "./mixins/i18n/I18n";
    import Spinner from './assets/three-dots.svg';
    import { AuthMixin } from "./mixins/authentication/Authentication";

    export default {
        name: 'app',
        props: {
            galleryId: {
                type: String,
                required: true
            },
        },
        components: {
            SearchInput,
            FilterButton,
            FilterModal,
            MapModal,
            Login,
            Cart,
            Header,
            DetailsInformation,
            Coupon,
            Spinner,
        },
        mixins: [
            GalleryConfigMixin,
            i18nMixin,
            AuthMixin
        ],
        data: () => {
            return ({
                coupons: [],
                filterData: [],
                openedCouponInfo: null,
                isCouponMapOpened: false,
                filterOpen: false,
                cart: {},
                filterState: {
                    sorting: 'range',
                    region: '',
                    networks: [],
                    networksToFilterBy: [],
                    medicines: false
                },
                filterInput: '',
                loadingData: false,
            });
        },
        methods: {
            openInfo(coupon) {
                this.openedCouponInfo = coupon;
            },
            closeInfo() {
                this.openedCouponInfo = null;
            },
            openMap() {
                this.isCouponMapOpened = true;
            },
            closeMap() {
                this.isCouponMapOpened = false;
            },
            toggleCouponInCart(coupon) {
                Vue.set(this.cart, coupon.id, !this.cart[coupon.id]);
            },
            extendCouponWithId(coupon) {
                return {
                    ...coupon,
                    id: `${coupon.offerId}_${coupon.networkIds[0]}`
                };
            },
            filterByNetworks({networksToFilterBy}) {
                if (!networksToFilterBy.length) {
                    return () => true;
                }

                const filterByNetworksMap = networksToFilterBy.reduce((acc, id) => ({...acc, [id]: true}), {});
                return ({networkIds}) =>
                    networkIds.some(id => {
                        return filterByNetworksMap[id];
                    });
            },
            filterByMedicines({medicines}) {
                if (!medicines) {
                    return () => true;
                }

                return ({isMed}) => isMed;
            },
            doesStringContainText(str, text) {
                if (!str) {
                    return false;
                }

                return str.toLowerCase().includes(text.trim().toLowerCase());
            },
            filterByText(filterInput) {
                return ({title, discountForString, promotionTitle, promotionMainText}) => {
                    return this.doesStringContainText(title, filterInput)
                        || this.doesStringContainText(discountForString, filterInput)
                        || this.doesStringContainText(promotionTitle, filterInput)
                        || this.doesStringContainText(promotionMainText, filterInput);
                };
            }
        },
        created() {
            this.$root.app = this;
        },
        mounted() {
            this.loadingData = true;

            http.get(`https://digitalsavertest.azurewebsites.net/api/gallery/Gallery`, {id: this.galleryId})
                .then(data => {
                    this.coupons = data.galleryItems.map(this.extendCouponWithId);
                    this.setGalleryConfig(data);

                    if (data.i18n) {
                        this.setI18N(data.i18n);
                    }

                    this.loadingData = false;
                })
                .catch(() => this.loadingData = false);

            if (this.config.isFiltersActive) {
                http.get(`https://digitalsavertest.azurewebsites.net/api/gallery/NetworkRegionFilters`, {id: this.galleryId})
                    .then(data => {
                        this.filterData = data;
                    });
            }
        },
        computed: {
            selectedCoupons() {
                return this.coupons.filter(coupon => this.cart[coupon.id]);
            },
            filteredCoupons() {
                const filterState = this.filterState;

                return this.coupons
                    .filter(this.filterByNetworks(filterState))
                    .filter(this.filterByMedicines(filterState))
                    .sort(sortOffers(filterState.sorting));
            },
            filterApplied() {
                const {sorting, region, networks, medicines} = this.filterState;
                return Boolean(
                    sorting !== 'range'
                    || region !== ''
                    || networks.length
                    || medicines
                );
            },
            filteredBySearch() {
                return this.filteredCoupons.filter(this.filterByText(this.filterInput));
            },
            cssVars() {
                if (!this.config) {
                    return {};
                }
                return {
                    '--adv-gallery-main-color': this.config.colors.primaryColor,
                    '--adv-gallery-main-darker-color': this.config.colors.darkerPrimaryColor,
                    '--adv-gallery-main-text-color': this.config.colors.primaryTextColor,
                    '--adv-gallery-secondary-text-color': this.config.colors.secondaryTextColor,
                    '--adv-gallery-secondary-border-color': this.config.colors.secondaryBorderColor,
                    color: this.config.colors.primaryTextColor
                }
            }
        },
    }
</script>

<style>
    @import url('https://fonts.googleapis.com/css?family=Roboto:300,400,400i,500,500i,700,700i&display=swap');

    .adv-gallery, .adv-gallery * {
        box-sizing: border-box;
        font-family: 'Roboto', sans-serif;
    }

    .adv-g__coupons-list {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        z-index: 1;
    }

    .adv-g__header_login {
        margin-left: 15px;
    }

    .adv-g__spinner {
        width: 120px;
        height: 30px;
        display: block;
        margin: 150px auto;
    }

    .adv-g__spinner * {
        fill: #bdbdbd;
    }
</style>
