<template>
    <div class="adv-g__autocomplete">
        <text-input
                class="adv-g__autocomplete__input"
                v-on:focus="setFocus(true)"
                v-on:blur="setFocus(false); selectItem();"
                v-model="inputValue"
                v-on:keyup.enter.native="onEnter"
                v-bind:placeholder="placeholder"
        ><slot></slot>
        </text-input>
        <Btn type="link"
             class="adv-g__autocomplete__clear"
             v-show="inputValue.length"
             v-on:click="clear"
        >
            <template v-slot:icon>
                <ClearSvg class="adv-btn__icon adv-g__autocomplete__clear__icon"></ClearSvg>
            </template>
        </Btn>
        <ul v-on:mouseenter="onMouseIn(true)"
            v-on:mouseleave="onMouseIn(false)"
            v-show="showOptions.length && (focused || mousein)"
            class="adv-g__autocomplete__options-list">
            <li class="adv-g__autocomplete__option"
                v-for="option in showOptions"
                v-bind:key="option"
                v-on:mousedown="onOptionClick(option)"
            >{{option}}
            </li>
        </ul>
    </div>
</template>

<script>
    import TextInput from "./TextInput";
    import ClearSvg from '../../assets/clear.svg';
    import Btn from "./Button";

    export default {
        name: "Autocomplete",
        components: {Btn, TextInput, ClearSvg},
        props: {
            value: {
                type: String
            },
            options: {
                type: Array,
                required: true
            },
            placeholder: String
        },
        data() {
            return {
                inputValue: this.value,
                focused: false,
                mousein: false
            }
        },
        computed: {
            filteredOptions() {
                const input = this.inputValue;
                return this.options.filter(option => {
                    return option.toLowerCase().includes(input.toLowerCase());
                });
            },
            showOptions() {
                const input = this.inputValue;
                return this.filteredOptions.filter(option => {
                    return option.trim() !== input.trim();
                });
            }
        },
        methods: {
            setFocus(v) {
                this.focused = v;
            },
            onEnter() {
                if (this.inputValue === '') {
                    return;
                }

                if (this.filteredOptions.length && this.filteredOptions.length === this.showOptions.length) {
                    this.inputValue = this.filteredOptions[0].trim();
                }
            },
            onOptionClick(option) {
                this.inputValue = option;
                this.onMouseIn(false);
            },
            onMouseIn(v) {
                this.mousein = v;
            },
            selectItem() {
                const toSelect = this.filteredOptions.find(option => option.toLowerCase().trim() === this.inputValue.toLowerCase().trim());
                this.inputValue = toSelect || '';
            },
            clear() {
                this.inputValue = '';
            }
        },
        watch: {
            value() {
                this.inputValue = this.value;
            },
            inputValue() {
                this.$emit('input', this.inputValue);
            }
        }
    }
</script>

<style scoped>
    .adv-g__autocomplete {
        position: relative;
    }

    .adv-g__autocomplete__clear {
        position: absolute;
        right: 20px;
        bottom: 13px;
        padding: 0;
        background: rgba(255, 255, 255, 0.8);
    }

    .adv-g__autocomplete__clear__icon {
        fill: var(--adv-gallery-secondary-text-color);
    }

    .adv-g__autocomplete__input {
        width: 100%;
        height: 64px;
    }

    .adv-g__autocomplete__options-list {
        position: absolute;
        top: calc(100% + 4px);
        border-radius: 4px;
        border: 1px solid var(--adv-gallery-secondary-border-color);
        box-shadow: 0 0 32px 0 rgba(0, 0, 0, 0.12);
        width: 100%;
        max-height: 232px;
        background: white;
        overflow-y: auto;
        margin: 0;
        padding: 8px 0;
    }

    .adv-g__autocomplete__option {
        list-style-type: none;
        margin: 0;
        padding: 8px 24px;
        cursor: pointer;
        height: 40px;
        line-height: 1.5rem;
        font-size: 1rem;
        vertical-align: center;
    }

    .adv-g__autocomplete__option:hover,
    .adv-g__autocomplete__option:focus {
        background-color: rgba(0, 0, 0, 0.08);
    }
</style>
