<template>
    <div class="adv-g__filter__sorting">
        <textblock
                class="adv-g__filter__sorting__label"
                size="small"
        >{{i18n.filter.sortByLabel}}</textblock>
        <RadioButton
                value="range"
                id="filter-by-range"
                v-model="sorting"
        >{{i18n.filter.sortByRangeLabel}}
        </RadioButton>
        <RadioButton
                value="date"
                id="filter-by-date"
                v-model="sorting"
        >{{i18n.filter.sortByDateLabel}}
        </RadioButton>
        <RadioButton
                value="alphabet"
                id="filter-by-alphabet"
                v-model="sorting"
        >{{i18n.filter.sortByAlphabetLabel}}
        </RadioButton>
        <RadioButton
                value="discount"
                id="filter-by-discount"
                v-model="sorting"
        >{{i18n.filter.sortByDiscountLabel}}
        </RadioButton>
    </div>
</template>

<script>
    import RadioButton from "../../ui/RadioButton";
    import Textblock from "../../ui/TextBlock";
    import {i18nAwareMixin} from "../../../mixins/i18n/I18nAware";

    export default {
        name: "Sorting",
        components: {Textblock, RadioButton},
        mixins: [i18nAwareMixin],
        props: {
            value: {
                type: String
            },
        },
        data() {
            return {
                sorting: this.value
            }
        },
        watch: {
            value() {
                this.sorting = this.value;
            },
            sorting() {
                this.$emit('input', this.sorting);
            }
        }
    }
</script>

<style scoped>
    .adv-g__filter__sorting > :first-child {
        display: block;
    }

    .adv-g__filter__sorting > * {
        margin-bottom: 0.75rem;
    }

    .adv-g__filter__sorting__label {
        color: var(--adv-gallery-secondary-text-color);
    }
</style>
