<template>
    <label class="adv-g__radio">
        <input
                class="adv-g__radio__input"
                type="radio"
                v-bind:name="value"
                v-bind:value="value"
                v-model="selectedValue"
                v-on:change="$emit('change', $event.target.value)"
        >
        <div class="adv-g__radio__fake-input"></div>
        <div class="adv-g__radio__label">
            <slot></slot>
        </div>
    </label>
</template>

<script>
    export default {
        name: "RadioButton",
        props: {
            value: {
                type: String,
                required: true
            },
            selected: {
                type: String,
            }
        },
        watch: {
            selected(v) {
                this.selectedValue = v;
            }
        },
        model: {
            prop: 'selected',
            event: 'change',
        },
        data() {
            return {
                selectedValue: this.selected,
            };
        },
    }
</script>

<style scoped>
    .adv-g__radio {
        position: relative;
        display: flex;
        align-items: center;
        height: 1.5rem;
        cursor: pointer;
    }

    .adv-g__radio__input {
        position: absolute;
        opacity: 0;
    }

    .adv-g__radio__fake-input {
        height: 20px;
        width: 20px;
        border: 1px solid var(--adv-gallery-secondary-border-color);
        border-radius: 100%;
    }

    .adv-g__radio__label {
        padding-left: 16px;
        line-height: 1rem;
    }

    .adv-g__radio__input:checked ~ .adv-g__radio__fake-input {
        border: 1px solid var(--adv-gallery-main-darker-color);
        box-shadow: inset 0px 0px 0px 5px var(--adv-gallery-main-color);
    }

    .adv-g__radio__input:focus ~ .adv-g__radio__fake-input {
        border: 1px solid var(--adv-gallery-main-color);
    }
</style>
