<template>
    <modal
            class="adv-g__filter-modal"
            v-on:close="$emit('close')"
            overflow="visible"
    >
        <template v-slot:title>
            <div class="adv-g__filter-modal__header">
                <span></span>
                <Btn type="danger_outline"
                     size="sm"
                     v-on:click="clearFilter"
                     v-if="filterChanged"
                     class="adv-g__filter-modal__clear-btn"
                >
                    <template v-slot:icon>
                        <CloseSvg class="adv-btn__icon"></CloseSvg>
                    </template>
                    <template v-slot:name>
                        {{i18n.filter.clearAllBtnLabel}}
                    </template>
                </Btn>
            </div>
        </template>
        <template v-slot:body>
            <div class="adv-g__filter-modal__body">
                <div class="adv-g__filter-modal__filters">
                    <Sorting
                            class="adv-g__filter-modal__sorting"
                            v-model="sorting"
                    ></Sorting>
                    <autocomplete
                            v-bind:options="availableRegions"
                            class="adv-g__filter-modal__region"
                            v-model="selectedRegion"
                    >{{i18n.filter.selectRegionLabel}}
                    </autocomplete>
                    <multiselect
                            v-bind:options="availableNetworks"
                            class="adv-g__filter-modal__networks"
                            v-model="selectedNetworks"
                            v-bind:option-title-fn="networkIdToTitleFn"
                    >{{i18n.filter.selectNetworksLabel}}
                    </multiselect>
                    <Checkbox
                            class="adv-g__filter-modal__medicines"
                            v-model="medicines">
                        {{i18n.filter.checkboxMedicinesCategory}}
                    </Checkbox>
                </div>
            </div>
        </template>
        <template v-slot:footer-left>
            <Btn
                    type="primary"
                    class="adv-g__filter-modal__submit"
                    size="lg"
                    v-on:click="applyFilter()"
            >
                <template v-slot:name>
                    <textblock size="small">{{i18n.filter.showResultsBtnLabel}}</textblock>
                </template>
            </Btn>
        </template>
    </modal>
</template>

<script>
    import Modal from "../../ui/Modal";
    import Btn from "../../ui/Button";
    import Textblock from "../../ui/TextBlock";
    import Sorting from "./Sorting";
    import Autocomplete from "../../ui/Autocomplete";
    import Multiselect from "../../ui/Multiselect";
    import Checkbox from "../../ui/Checkbox";
    import {FilterMixin} from "../../../mixins/Filter";
    import CloseSvg from '../../../assets/close.svg';
    import {i18nAwareMixin} from "../../../mixins/i18n/I18nAware";

    export default {
        name: "filter-modal",
        mixins: [FilterMixin, i18nAwareMixin],
        components: {
            Checkbox,
            Multiselect,
            Autocomplete,
            Sorting,
            Textblock,
            Btn,
            Modal,
            CloseSvg
        },
        props: {
            filterState: {
                type: Object,
                required: true
            }
        },
        model: {
            prop: 'filterState',
            event: 'filterApplied'
        },
        data() {
            return {
                sorting: this.filterState.sorting,
                medicines: this.filterState.medicines
            }
        },
        methods: {
            applyFilter() {
                const networksToFilterBy = this.selectedNetworks.length
                    ? [...this.selectedNetworks]
                    : [...this.availableNetworks];

                this.$emit('filterApplied', {
                    sorting: this.sorting,
                    region: this.selectedRegion,
                    networks: [...this.selectedNetworks],
                    medicines: this.medicines,
                    networksToFilterBy
                });
            },
            clearFilter() {
                this.selectedRegion = '';
                this.selectedNetworks = [];
                this.medicines = false;
                this.sorting = 'range';
            }
        },
        computed: {
            filterChanged() {
                return Boolean(
                    this.sorting !== 'range'
                    || this.selectedRegion !== ''
                    || this.selectedNetworks.length
                    || this.medicines
                );
            },
        }

    }
</script>

<style scoped>
    .adv-g__filter-modal {
        width: 420px;
        max-width: 100vw;
        height: 616px;
        overflow-y: visible;
    }

    .adv-g__filter-modal__header {
        display: flex;
        align-items: center;
    }

    .adv-g__filter-modal__clear-btn {
        margin-left: 10px;
    }

    .adv-g__filter-modal__body {
        display: flex;
        flex-direction: column;
        align-items: stretch;
        justify-content: space-between;
        overflow-y: visible;
    }

    .adv-g__filter-modal__filters {
        flex-grow: 1;
    }

    .adv-g__filter-modal__submit {
        margin-top: auto;
        width: 100%;
    }

    .adv-g__filter-modal__sorting {
        margin-bottom: 0.8rem;
    }

    .adv-g__filter-modal__region {
        width: 100%;
        margin-bottom: 16px;
        z-index: 102;
    }

    .adv-g__filter-modal__networks {
        z-index: 101;
        margin-bottom: 16px;
    }

    .adv-g__filter-modal__medicines {
        z-index: 100;
    }
</style>
