import {downloadFile} from "../utils/download-file";

export const DownloadFileMixin = {
    data() {
      return {
          loading: false,
      };
    },
    computed: {
        pdfUrl() {
            return `https://digitalsavertest.azurewebsites.net/api/digitalOffers/GetOfferPDF?digitalOfferId=${this.offer.offerId}&networkId=${this.offer.networkIds[0]}`;
        },
    },
    methods: {
        downloadFile() {
            this.loading = true;

            downloadFile(this.pdfUrl)
                .then(() => (this.loading = false));
        }
    }
};