<template>
    <div class="adv-g__multiselect" v-on:click.stop="dropdownOpened = true">
        <label class="adv-g__multiselect__label">
            <textblock
                    class="adv-g__multiselect__label__text"
                    v-if="$slots.default"
                    size="small">
                <slot></slot>
            </textblock>
        </label>
        <div class="adv-g__multiselect__input">
            <div class="adv-g__multiselect__input__placeholder"
                 v-if="!selectedOptions.length"
            >
                {{placeholder}}
            </div>
            <div v-for="item in selectedOptions"
                 v-bind:key="optionTitleFn ? optionTitleFn(item) : item"
                 class="adv-g__multiselect__input__item"
            >
                <div class="adv-g__multiselect__input__item__text">
                    {{optionTitleFn ? optionTitleFn(item) : item}}
                </div>
                <Btn type="link"
                     size="sm"
                     class="adv-g__multiselect__input__item__remove"
                     v-on:click="remove(item)"
                >
                    <template v-slot:icon>
                        <CloseSvg class="adv-btn__icon"></CloseSvg>
                    </template>
                </Btn>
            </div>
        </div>
        <ul v-on:mouseenter="onMouseIn(true)"
            v-on:mouseleave="onMouseIn(false)"
            v-show="showOptions.length && dropdownOpened"
            class="adv-g__multiselect__options-list">
            <li class="adv-g__multiselect__option"
                v-for="option in showOptions"
                v-bind:key="option"
                v-on:mousedown="onOptionClick(option)"
            >{{optionTitleFn ? optionTitleFn(option) : option}}
            </li>
        </ul>
    </div>
</template>

<script>
    import Textblock from "./TextBlock";
    import Btn from "./Button";
    import CloseSvg from '../../assets/close.svg';

    export default {
        name: "Multiselect",
        components: {Btn, Textblock, CloseSvg},
        props: {
            value: {
                type: Array,
                required: true
            },
            options: {
                type: Array,
                required: true
            },
            optionTitleFn: {
                type: Function,
                required: false
            },
            placeholder: String
        },
        data() {
            return {
                selectedOptions: this.value,
                focused: false,
                mousein: false,
                dropdownOpened: false,
            }
        },
        computed: {
            showOptions() {
                const selectedOptions = this.selectedOptions;
                return this.options.filter(option => {
                    return !selectedOptions.includes(option);
                });
            }
        },
        methods: {
            setFocus(v) {
                this.focused = v;
            },
            onOptionClick(option) {
                this.selectedOptions.push(option);
            },
            onMouseIn(v) {
                this.mousein = v;
            },
            remove(option) {
                this.selectedOptions = this.selectedOptions.filter(o => o !== option);
            },
            onDocumentClick() {
                this.dropdownOpened = false;
            }
        },
        watch: {
            value() {
                this.selectedOptions = this.value;
            },
            selectedOptions() {
                this.$emit('input', this.selectedOptions);
            },
            dropdownOpened() {
                if (this.dropdownOpened) {
                    document.addEventListener('click', this.onDocumentClick);
                } else {
                    document.removeEventListener('click', this.onDocumentClick);
                }
            }
        },
        beforeDestroy() {
            this.dropdownOpened = false;
        }
    }
</script>

