<template>
    <div class="adv-g__cart-dropdown" v-on:click.stop="">
        <div class="adv-g__cart-dropdown__list">
            <template v-for="(coupon, index) in coupons">
                <cart-list-item
                        v-bind:key="coupon.id"
                        v-bind:coupon="coupon"
                        v-on:remove="$emit('remove', coupon)"
                ></cart-list-item>
                <delimiter v-if="index !== (coupons.length-1)"
                           v-bind:key="coupon.id + 'delimiter'"
                           class="adv-g__cart-list__delimiter"></delimiter>
            </template>
        </div>
        <Btn type="primary"
             v-if="coupons.length"
             class="adv-g__dropdown__download-btn"
             v-on:click="$emit('download')"
        >
            <template v-slot:icon>
                <DownloadSvg
                        v-if="!loadingPdf"
                        class="adv-btn__icon"
                ></DownloadSvg>
                <Spinner
                        v-if="loadingPdf"
                        class="adv-btn__icon"
                ></Spinner>
            </template>
            <template v-slot:name>
                <span>{{i18n.cart.download}}</span>
            </template>
        </Btn>
    </div>
</template>

<script>
    import CartListItem from "./CartIListItem";
    import Btn from "../../ui/Button";
    import DownloadSvg from '../../../assets/download.svg';
    import Delimiter from "../../ui/Delimiter";
    import Spinner from '../../../assets/anitmated-spinner.svg';
    import {i18nAwareMixin} from "../../../mixins/i18n/I18nAware";

    export default {
        name: "cart-list",
        components: {Delimiter, Btn, DownloadSvg, CartListItem, Spinner},
        mixins: [i18nAwareMixin],
        props: {
            coupons: {
                type: Array,
                default() {
                    return [];
                }
            },
            loadingPdf: {
                type: Boolean,
                default: false,
            }
        }
    }
</script>

