import { initFbSdk } from "../../utils/facebook-login-setup";
import * as http from '../../utils/http';
import { initGoogleSdk, logoutWithGoogle } from "../../utils/google-login-setup";

const UserAuthDataToken = 'adv-gallery-user-auth-data';

export const AuthMixin = {
    props: {
        facebookAppId: {
            type: String
        },
        googleClientId: {
            type: String
        }
    },
    data() {
        const userData = JSON.parse(localStorage.getItem(UserAuthDataToken) || '{}');
        const userIsLoggedIn = userData.expires && userData.expires > new Date().getTime();
        const email = userData.email;

        const fbLoginEnabled = !!this.facebookAppId;
        const googleLoginEnabled = !!this.googleClientId;

        return {
            fb: {
                id: '',
                profilePicture: '',
                name: '',
                loggedIn: false,
                accessToken: '',
                loading: false,
                enabled: fbLoginEnabled
            },
            google: {
                name: '',
                loggedIn: false,
                initialized: false,
                enabled: googleLoginEnabled,
                profilePicture: '',
                accessToken: '',
                email: ''
            },
            custom: {
                loggedIn: userIsLoggedIn || false,
                email: email || '',
            }
        };
    },
    computed: {
        username() {
            if (this.fb.loggedIn) {
                return this.fb.name;
            }

            if (this.google.loggedIn) {
                return this.google.name;
            }

            return this.custom.email;
        },
        profilePicture() {
            if (this.fb.loggedIn) {
                return this.fb.profilePicture;
            }

            if (this.google.loggedIn) {
                return this.google.profilePicture;
            }

            return undefined;
        },
        userIsLoggedIn() {
            return this.fb.loggedIn
                || this.custom.loggedIn
                || this.google.loggedIn;
        }
    },
    mounted() {
        if (this.facebookAppId) {
            initFbSdk(this.facebookAppId).then(() => {
                this.checkFbLoginStatus();
            });
        }

        if (this.googleClientId) {
            initGoogleSdk(this.googleClientId).then((user) => this.onUserLoggedInWithGoogle(user));
        }
    },
    methods: {
        checkGoogleLoginStatus() {

        },
        checkFbLoginStatus() {
            this.fbLoading(true)();

            return new Promise(resolve => window.FB.getLoginStatus(resolve))
                .then(({status, authResponse}) => {
                    const fbLogin = status === 'connected';
                    this.fb.loggedIn = fbLogin;

                    if (!fbLogin) {
                        this.resetFbState();
                        return Promise.reject();
                    }

                    this.fb.accessToken = authResponse.accessToken;
                    this.fb.id = authResponse.userID;
                })
                .then(() => this.getFBProfileInfo())
                .then(({picture: {data: {url}}, name}) => {
                    this.fb.profilePicture = url;
                    this.fb.name = name;
                })
                .finally(this.fbLoading(false));
        },
        fbLoading(state) {
            return () => this.loading = state;
        },
        resetFbState() {
            this.fb = {
                id: '',
                profilePicture: '',
                name: '',
                loggedIn: false,
                accessToken: '',
                enabled: !!this.facebookAppId
            };
        },
        resetGoogleState() {
            this.google = {
                name: '',
                loggedIn: false,
                initialized: false,
                enabled: !!this.googleClientId,
                profilePicture: '',
                accessToken: '',
                email: ''
            };
        },
        getFBProfileInfo() {
            return new Promise(resolve => window.FB.api(`/${this.fb.id}`, {
                fields: ['name', 'picture'],
                redirect: false
            }, resolve));
        },
        onUserLoggedIn(info, email) {
            localStorage.setItem(UserAuthDataToken, JSON.stringify({
                ...info,
                expires: new Date().getTime() + (info.expires_in * 1000),
                email,
            }));

            this.custom.loggedIn = true;
            this.custom.email = email;
        },
        onUserLoggedInWithGoogle(user) {
            this.google.initialized = true;
            this.google.loggedIn = !!user;

            if (user) {
                const profile = user.getBasicProfile();
                this.google.name = profile.getName();
                this.google.email = profile.getEmail();
                this.google.profilePicture = profile.getImageUrl();
            }
        },
        onUserLoggedOut() {
            if (this.fb.loggedIn) {
                this.doLogoutWithFacebook();
            } else if (this.google.loggedIn) {
                this.doLogoutWithGoogle();
            } else {
                this.doCustomLogout();
            }
        },
        doCustomLogout() {
            localStorage.removeItem(UserAuthDataToken);
            this.custom.loggedIn = false;
        },
        login(email, password) {
            return http
                .post('https://digitalsavertest.azurewebsites.net/api/auth/login', {
                    userName: email,
                    password: password
                })
                .then(response => JSON.parse(response))
                .then(({data, isError}) => {
                    if (isError) {
                        throw new Error();
                    }

                    this.onUserLoggedIn(JSON.parse(data), email);
                });
        },
        register(email, password) {
            return http
                .post('https://digitalsavertest.azurewebsites.net/api/account/RegisterUser', {
                    email,
                    password
                })
                .then(response => JSON.parse(response))
                .then(({isError}) => isError ? Promise.reject() : Promise.resolve());
        },
        resetPassword(email) {
            return http
                .post(`https://digitalsavertest.azurewebsites.net/api/account/ForgotPassword?email=${email}`)
                .then(response => JSON.parse(response))
                .then(({isError}) => isError ? Promise.reject() : Promise.resolve());
        },
        loginWithFacebook() {
            return new Promise(resolve => {
                window.FB.login(resolve, {scope: 'public_profile,email'});
            })
                .then(() => this.checkFbLoginStatus());
        },
        doLogoutWithFacebook() {
            new Promise(resolve => window.FB.logout(resolve))
                .then(() => this.checkFbLoginStatus());
        },
        doLogoutWithGoogle() {
            logoutWithGoogle()
                .then(() => this.resetGoogleState());
        }
    }
};
