<template>
    <Btn
            type="link"
            v-on:click="copyToClipboard(text)"
    >
        <template v-slot:icon>
            <CopySvg v-if="!copied" class="adv-btn__icon adv-g__copy-to-clipboard-icon"></CopySvg>
            <CheckCircleSvg v-if="copied" class="adv-btn__icon adv-g__copy-to-clipboard-icon"></CheckCircleSvg>
        </template>
    </Btn>
</template>

<script>
    import Btn from "../../../ui/Button";
    import CopySvg from '../../../../assets/cabinet-copy-3.svg';
    import CheckCircleSvg from '../../../../assets/check_circle.svg';

    export default {
        name: "CopyToClipboard",
        components: {Btn, CopySvg, CheckCircleSvg},
        props: {
            text: {
                type: String,
                required: true
            },
        },
        data() {
            return {
                copied: false,
            }
        },
        methods: {
            copyToClipboard(str) {
                const el = document.createElement('textarea');
                el.value = str;
                el.setAttribute('readonly', '');
                el.style.position = 'absolute';
                el.style.left = '-9999px';
                document.body.appendChild(el);
                const selected =
                    document.getSelection().rangeCount > 0
                        ? document.getSelection().getRangeAt(0)
                        : false;
                el.select();
                document.execCommand('copy');
                document.body.removeChild(el);
                if (selected) {
                    document.getSelection().removeAllRanges();
                    document.getSelection().addRange(selected);
                }

                this.copied = true;
                setTimeout(() => this.copied = false, 5000);
            }
        }
    }
</script>

<style scoped>
    .adv-g__copy-to-clipboard-icon {
        fill: var(--adv-gallery-secondary-text-color);
    }
</style>
