<template>
    <div class="adv-g_login"
         v-on:click.stop=""
    >
        <Btn
                type="link"
                v-if="userIsLoggedIn"
                v-on:click="toggleUserDropdown"
                key="logged-in"
        >
            <template v-slot:icon>
                <div class="adv-g__login__icon"
                     v-if="!profilePicture"
                >
                    <UserSvg class="adv-btn__icon"></UserSvg>
                </div>
                <div class="adv-g__login__profile__picture"
                     v-if="profilePicture"
                >
                    <img
                            v-bind:src="profilePicture"
                            class="adv-g__login__profile__picture__image">
                </div>
            </template>
        </Btn>
        <Btn
                class="adv-g_login__btn"
                type="link"
                v-if="!userIsLoggedIn"
                v-on:click="toggleLoginForm"
                key="not-logged-in"
        >
            <template v-slot:icon>
                <div class="adv-g__login__icon">
                    <UserSvg class="adv-btn__icon adv-g__login__icon_not_logged_in"></UserSvg>
                </div>
            </template>
            <template v-slot:name>
                {{i18n.authentication.loginBtnLabel}}
            </template>
        </Btn>
        <login-form
                v-if="!userIsLoggedIn"
                v-show="loginFormOpened"
        ></login-form>
        <user-dropdown
                v-if="userIsLoggedIn"
                v-show="userDropdownOpened"
                v-bind:username="username"
        ></user-dropdown>
    </div>
</template>

<script>
    import Btn from "../../ui/Button";
    import UserSvg from '../../../assets/user.svg';
    import LoginForm from "./LoginForm";
    import UserDropdown from "./UserDropdown";
    import { i18nAwareMixin } from "../../../mixins/i18n/I18nAware";
    import { AuthAwareMixin } from "../../../mixins/authentication/AuthenticationAware";

    export default {
        name: "login",
        components: { UserDropdown, LoginForm, Btn, UserSvg },
        mixins: [i18nAwareMixin, AuthAwareMixin],
        data() {
            return {
                loginFormOpened: false,
                userDropdownOpened: false
            };
        },
        methods: {
            toggleLoginForm() {
                this.loginFormOpened = !this.loginFormOpened;
            },
            toggleUserDropdown() {
                this.userDropdownOpened = !this.userDropdownOpened;
            },
            onDocumentClick() {
                this.loginFormOpened = false;
                this.userDropdownOpened = false;
            },
            updateListeningToDocumentClick(modalOpen) {
                if (modalOpen) {
                    document.addEventListener('click', this.onDocumentClick);
                } else {
                    document.removeEventListener('click', this.onDocumentClick);
                }
            }
        },
        computed: {
            userIsLoggedIn() {
                return this.auth.userIsLoggedIn;
            },
            profilePicture() {
                return this.auth.profilePicture;
            },
            username() {
                return this.auth.username;
            }
        },
        watch: {
            userIsLoggedIn() {
                this.loginFormOpened = false;
                this.userDropdownOpened = false;
            },
            userDropdownOpened() {
                this.updateListeningToDocumentClick(this.userDropdownOpened);
            },
            loginFormOpened() {
                this.updateListeningToDocumentClick(this.loginFormOpened);
            }
        },
        beforeDestroy() {
            this.loginFormOpened = false;
            this.userDropdownOpened = false;
        }
    }
</script>

<style scoped>
    .adv-g_login {
        position: relative;
        min-width: 76px;
    }

    .adv-g__login__icon {
        background: white;
        border-radius: 50%;
        padding: 10px;
        display: inline-block;
    }

    .adv-g__login__icon_not_logged_in * {
        fill: var(--adv-gallery-secondary-text-color);
    }

    .adv-g__login__profile__picture {
        border-radius: 100%;
    }

    .adv-g__login__profile__picture__image {
        border-radius: 100%;
        max-height: 65px;
    }
</style>
