<template>
    <div class="adv-g__auth-modal">
        <div class="adv-g__auth-modal__tabs"
             v-if="selectedTab=== 'login' || selectedTab === 'register'"
        >
            <button class="adv-g__auth-modal__tab"
                    v-bind:class="{'adv-g__auth-modal__tab_active': selectedTab === 'login'}"
                    v-on:click="selectedTab = 'login'"
            >
                {{i18n.authentication.loginBtnLabel}}
            </button>
            <button class="adv-g__auth-modal__tab"
                    v-bind:class="{'adv-g__auth-modal__tab_active': selectedTab === 'register'}"
                    v-on:click="selectedTab = 'register'"
            >
                {{i18n.authentication.registerBtnLabel}}
            </button>
        </div>
        <div class="adv-g__auth-modal__panels">
            <form class="adv-g__login-form" v-if="selectedTab === 'login'" novalidate>
                <div
                        class="adv-g__login-form__error"
                        v-if="showLoginError"
                >
                    {{i18n.authentication.wrongPasswordMessage}}
                </div>
                <div
                        class="adv-g__login-form__error"
                        v-if="showRegistrationError"
                >
                    {{i18n.authentication.registrationFailedMessage}}
                </div>
                <label
                        v-bind:for="'adv-gallery-login-form-email-' + $root.galleryId"
                        class="adv-g__login-form_label"
                >
                    {{i18n.authentication.emailLabel}}
                </label>
                <input
                        v-bind:id="'adv-gallery-login-form-email-' + $root.galleryId"
                        class="adv-g__login-form__input"
                        type="email"
                        autocomplete="username"
                        v-model="loginForm.email"
                >
                <label
                        v-bind:for="'adv-gallery-login-form-password-' + $root.galleryId"
                        class="adv-g__login-form_label"
                >
                    {{i18n.authentication.passwordLabel}}
                </label>
                <input
                        v-bind:id="'adv-gallery-login-form-password-' + $root.galleryId"
                        class="adv-g__login-form__input"
                        type="password"
                        autocomplete="current-password"
                        v-model="loginForm.password"
                >
                <Btn
                        type="link"
                        v-on:click="onForgotPassword"
                        class="adv-g__forgot-password-btn"
                >
                    <template v-slot:name>
                        {{i18n.authentication.forgotPassword}}
                    </template>
                </Btn>
                <div
                        class="adv-g__login-form__error"
                        v-if="showPasswordLengthError"
                >
                    {{i18n.authentication.minimalLengthOfPasswordMessage}}
                </div>
                <div class="adv-g_login-form__footer">
                    <Btn
                            type="primary"
                            v-on:click="onLogin"
                    >
                        <template v-slot:name>
                            {{i18n.authentication.loginBtnLabel}}
                        </template>
                    </Btn>
                    <div class="adv-g_login-form__footer__right">
                        <LoginWithFbButton></LoginWithFbButton>
                        <LoginWithGoogleButton></LoginWithGoogleButton>
                    </div>
                </div>
            </form>
            <form class="adv-g__login-form" v-if="selectedTab === 'register'" novalidate>
                <div
                        class="adv-g__login-form__error"
                        v-if="showLoginError"
                >
                    {{i18n.authentication.wrongPasswordMessage}}
                </div>
                <div
                        class="adv-g__login-form__error"
                        v-if="showRegistrationError"
                >
                    {{i18n.authentication.registrationFailedMessage}}
                </div>
                <label
                        v-bind:for="'adv-gallery-login-form-email-' + $root.galleryId"
                        class="adv-g__login-form_label"
                >
                    {{i18n.authentication.emailLabel}}
                </label>
                <input
                        v-bind:id="'adv-gallery-login-form-email-' + $root.galleryId"
                        class="adv-g__login-form__input"
                        type="email"
                        autocomplete="username"
                        v-model="loginForm.email"
                >
                <label
                        v-bind:for="'adv-gallery-login-form-password-' + $root.galleryId"
                        class="adv-g__login-form_label"
                >
                    {{i18n.authentication.passwordLabel}}
                </label>
                <input
                        v-bind:id="'adv-gallery-login-form-password-' + $root.galleryId"
                        class="adv-g__login-form__input"
                        type="password"
                        autocomplete="current-password"
                        v-model="loginForm.password"
                >
                <label
                        v-bind:for="'adv-gallery-login-form-password-' + $root.galleryId"
                        class="adv-g__login-form_label"
                >
                    {{i18n.authentication.passwordRepeatLabel}}
                </label>
                <input
                        v-bind:id="'adv-gallery-login-form-password-' + $root.galleryId"
                        class="adv-g__login-form__input"
                        type="password"
                        autocomplete="current-password"
                        v-model="loginForm.passwordRepeat"
                >
                <div
                        class="adv-g__login-form__error"
                        v-if="showPasswordLengthError"
                >
                    {{i18n.authentication.minimalLengthOfPasswordMessage}}
                </div>
                <div
                        class="adv-g__login-form__error"
                        v-if="passwordsDoNotMatch"
                >
                    {{i18n.authentication.passwordsDoNotMatchMessage}}
                </div>
                <div class="adv-g_login-form__footer">
                    <Btn
                            type="primary"
                            v-on:click="onRegister"
                    >
                        <template v-slot:name>
                            {{i18n.authentication.registerBtnLabel}}
                        </template>
                    </Btn>
                </div>
            </form>
            <form class="adv-g__login-form" v-if="selectedTab === 'forgot_password'" novalidate>
                <template v-if="showPasswordResetMessage">
                    <div class="adv-g__login-form__message">
                        <textblock>{{i18n.authentication.newPasswordSentMessage}}</textblock>
                    </div>
                </template>
                <template v-if="!showPasswordResetMessage">
                    <label
                            v-bind:for="'adv-gallery-login-form-email-' + $root.galleryId"
                            class="adv-g__login-form_label"
                    >
                        {{i18n.authentication.emailLabel}}
                    </label>
                    <input
                            v-bind:id="'adv-gallery-login-form-email-' + $root.galleryId"
                            class="adv-g__login-form__input"
                            type="email"
                            autocomplete="username"
                            v-model="loginForm.email"
                    >
                </template>
                <div
                        class="adv-g__login-form__error"
                        v-if="showPasswordResetFailedMessage"
                >
                    {{i18n.authentication.passwordResetFailedMessage}}
                </div>
                <div class="adv-g_login-form__footer">
                    <template v-if="!showPasswordResetMessage">
                        <Btn
                                type="primary"
                                v-on:click="onSendNewPassword"
                        >
                            <template v-slot:name>
                                {{i18n.authentication.sendNewPassword}}
                            </template>
                        </Btn>
                        <Btn
                                type="link"
                                v-on:click="onCancelPasswordReset"
                        >
                            <template v-slot:name>
                                {{i18n.authentication.cancelSendingNewPassword}}
                            </template>
                        </Btn>
                    </template>
                    <template v-if="showPasswordResetMessage">
                        <Btn
                                type="primary"
                                v-on:click="onPasswordResetMessageRead"
                        >
                            <template v-slot:name>
                                {{i18n.authentication.loginBtnLabel}}
                            </template>
                        </Btn>
                    </template>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
    import Btn from "../../ui/Button";
    import {i18nAwareMixin} from "../../../mixins/i18n/I18nAware";
    import LoginWithFbButton from "./LoginWithFbButton";
    import LoginWithGoogleButton from "./LoginWithGoogleButton";
    import {AuthAwareMixin} from "../../../mixins/authentication/AuthenticationAware";
    import Textblock from "../../ui/TextBlock";

    export default {
        name: "LoginForm",
        components: {Textblock, LoginWithFbButton, LoginWithGoogleButton, Btn},
        mixins: [i18nAwareMixin, AuthAwareMixin],
        data() {
            return {
                loginForm: {
                    email: '',
                    password: '',
                    passwordRepeat: ''
                },
                showRegistrationError: false,
                showLoginError: false,
                showPasswordLengthError: false,
                showPasswordResetMessage: false,
                showPasswordResetFailedMessage: false,
                passwordsDoNotMatch: false,
                selectedTab: 'login',
            }
        },
        methods: {
            onLogin() {
                this.resetErrors();

                if (!this.loginForm.email) {
                    return;
                }

                if (!this.loginForm.password) {
                    return;
                }

                this.auth
                    .login(this.loginForm.email, this.loginForm.password)
                    .catch(() => (this.showLoginError = true));
            },
            onForgotPassword() {
                this.selectedTab = 'forgot_password';
            },
            onPasswordResetMessageRead() {
                this.selectedTab = 'login';
                this.resetErrors();
            },
            onSendNewPassword() {
                this.resetErrors();

                if (!this.loginForm.email) {
                    return;
                }

                this.auth.resetPassword(this.loginForm.email)
                    .then(() => {
                        this.showPasswordResetMessage = true;
                    })
                    .catch(() => {
                        this.showPasswordResetFailedMessage = true;
                    });
            },
            onCancelPasswordReset() {
                this.selectedTab = 'login';
                this.resetErrors();
            },
            onRegister() {
                this.resetErrors();

                if (!this.loginForm.email) {
                    return;
                }

                if (!this.loginForm.password || this.loginForm.password.length < 8) {
                    this.showPasswordLengthError = true;
                    return;
                }

                if (this.loginForm.password !== this.loginForm.passwordRepeat) {
                    this.passwordsDoNotMatch = true;
                    return;
                }

                this.auth.register(this.loginForm.email, this.loginForm.password)
                    .then(() => this.onLogin())
                    .catch(() => this.showRegistrationError = true);
            },
            resetErrors() {
                this.showRegistrationError = false;
                this.showPasswordLengthError = false;
                this.showLoginError = false;
                this.passwordsDoNotMatch = false;
                this.showPasswordResetMessage = false;
                this.showPasswordResetFailedMessage = false;
            },
        }
    }
</script>

<style scoped>
    .adv-g__auth-modal {
        position: absolute;
        right: 0;
        top: calc(100% + 10px);
        width: 300px;
        max-width: calc(100vw - 40px);
        background: white;
        border: 1px solid var(--adv-gallery-main-color);
        border-radius: 4px;
        box-shadow: 1px 1px 17px -8px rgba(0, 0, 0, 0.75);
    }

    .adv-g__auth-modal__tabs {
        width: 100%;
        display: flex;
        justify-content: stretch;
    }

    .adv-g__auth-modal__tab {
        display: inline-block;
        cursor: pointer;
        flex-grow: 1;
        display: flex;
        justify-content: center;
        padding: 12px;
        border: none;
        font-size: 1rem;
        border-top-left-radius: 3px;
        border-top-right-radius: 3px;
    }

    .adv-g__auth-modal__tab:not(.adv-g__auth-modal__tab_active) {
        border-bottom: 1px solid var(--adv-gallery-secondary-border-color);
    }

    .adv-g__auth-modal__tab_active {
        background: var(--adv-gallery-main-color);
        color: white;
        cursor: default;
    }

    .adv-g__login-form {
        padding: 20px;
    }

    .adv-g__login-form_label {
        font-size: 1rem;
        font-weight: 500;
        margin-bottom: 5px;
        display: inline-block;
    }

    .adv-g__login-form__input {
        width: 100%;
        border-radius: 4px;
        border: 1px solid #d5d5d5;
        font-size: 1rem;
        line-height: 2rem;
        margin-bottom: 20px;
        padding-left: 10px;
    }

    .adv-g__forgot-password-btn {
        padding: 0;
        margin-bottom: 20px;
    }

    .adv-g__login-form__error {
        font-size: 0.9rem;
        display: inline-block;
        margin-bottom: 15px;
        margin-top: -10px;
        color: red;
    }

    .adv-g_login-form__footer {
        display: flex;
        justify-content: space-between;
    }

    .adv-g_login-form__footer__right {
        display: flex;
        align-items: center;
    }

    .adv-g_login-form__footer__right > * {
        margin-left: 10px;
    }

    .adv-g__login-form__message {
        margin-bottom: 20px;
    }
</style>
