import {flattenReducer} from "../utils/array";

export const FilterMixin = {
    props: {
        filterState: {
            type: Object,
            default() {
                return {
                    region: '',
                    networks: []
                };
            }
        },
        filterData: {
            type: Array,
            required: true
        },
    },
    data() {
        return {
            selectedRegion: this.filterState.region,
            selectedNetworks: this.filterState.networks,
        };
    },
    computed: {
        regions() {
            const flatten = this.filterData
                .map(({regions}) => regions)
                .reduce(flattenReducer, []);

            return Array.from(new Set(flatten));
        },
        regionToNetworks() {
            return this.filterData.reduce((acc, {networkId, regions}) => {
                regions.forEach(region => {
                    acc[region] = Object.assign(acc[region] || {}, {[networkId]: true});
                });
                return acc;
            }, {});
        },
        networkIdToTitle() {
            return this.filterData.reduce((acc, {networkId, networkTitle}) => {
                return Object.assign(acc, {[networkId]: networkTitle});
            }, {});
        },
        networkIdToTitleFn() {
            const networkIdToTitle = this.networkIdToTitle;
            return (id) => networkIdToTitle[id];
        },
        availableRegions() {
            return this.regions.filter(
                region => {
                    return !this.selectedNetworks.length
                        || this.selectedNetworks.every(networkId => this.regionToNetworks[region][networkId]);
                }
            );
        },
        availableNetworks() {
            return this.filterData.filter(
                ({networkId}) => {
                    return !this.selectedRegion
                        || !this.regionToNetworks[this.selectedRegion]
                        || this.regionToNetworks[this.selectedRegion][networkId];
                }
            ).map(({networkId: id}) => id);
        }
    }
};