<template>
    <div>
        <div class="adv-g__modal"
        v-bind:class="{'adv-g__modal_overflow-visible': overflow === 'visible'}">
            <div class="adv-g__modal__guts">
                <div class="adv-g__modal__header">
                    <slot name="title"></slot>
                </div>
                <div
                        class="adv-g__modal__body"
                        v-bind:class="{
                        'adv-g__modal__body_w-out-footer': !footerPresent
                        }"
                >
                    <slot name="body"></slot>
                </div>
                <div class="adv-g__modal__footer" v-if="footerPresent">
                    <slot name="footer-left"></slot>
                    <div class="adv-g__modal__footer__right">
                        <slot name="footer-right"></slot>
                    </div>
                </div>
            </div>
            <Btn
                    type="link"
                    class="adv-g__modal__close"
                    v-on:click="$emit('close')"
            >
                <template v-slot:icon>
                    <CloseSvg class="adv-btn__icon"></CloseSvg>
                </template>
            </Btn>
        </div>
        <div class="adv-g__modal-overlay" id="adv-g__modal-overlay"></div>
    </div>
</template>

<script>
    import Btn from "./Button";
    import CloseSvg from '../../assets/close.svg';

    export default {
        name: "Modal",
        components: { Btn, CloseSvg },
        computed: {
            footerPresent() {
                return this.$slots['footer-left'] || this.$slots['footer-right'];
            }
        },
        props: {
            overflow: {
                type: String,
                validator(value) {
                    return !value || value === 'visible';
                }
            }
        }
    }
</script>

