<template>
    <div
            class="adv-g__block"
            v-bind:class="colorClass"
    >
        <slot></slot>
    </div>
</template>

<script>
    export default {
        name: "Block",
        props: {
            color: {
                type: String,
                default: 'secondary',
                validator(input) {
                    return ['secondary', 'primary'].includes(input);
                }
            }
        },
        computed: {
            colorClass() {
                return 'adv-g__block_' + this.color;
            }
        }
    }
</script>

<style scoped>
    .adv-g__block {
        border: 0;
        border-radius: 4px;
    }

    .adv-g__block_primary {
        background: var(--adv-gallery-main-color);
    }

    .adv-g__block_secondary {
        background: #f5f5f5;
    }
</style>
