<template>
    <div class="adv-g__popover" v-on:click.stop="">
        <div v-on:click="togglePopover">
            <slot name="element"></slot>
        </div>
        <div class="adv-g__popover__content" v-if="open">
            <slot name="popover-content"></slot>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Popover",
        data() {
            return {
                open: false
            }
        },
        methods: {
            togglePopover() {
                this.open = !this.open;
            },
            onDocumentClick() {
                this.open = false;
            },
            updateListeningToDocumentClick(modalOpen) {
                if (modalOpen) {
                    document.addEventListener('click', this.onDocumentClick);
                } else {
                    document.removeEventListener('click', this.onDocumentClick);
                }
            }
        },
        watch: {
            open() {
                this.updateListeningToDocumentClick(this.open);
            }
        }
    }
</script>

<style scoped>
    .adv-g__popover {
        position: relative;
        overflow: visible;
    }
    .adv-g__popover__content {
        display: block;
        position: absolute;
        top: calc(100% + 10px);
        right: 0;
        background: white;
        border: 1px solid var(--adv-gallery-secondary-border-color);
        border-radius: 4px;
        box-shadow: 1px 1px 17px -8px rgba(0, 0, 0, 0.75);
    }
</style>
