<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
    <popover>
        <template v-slot:element>
            <Btn type="secondary_outline"
                 v-on:click="onShareBtnFirstOpen"
            >
                <template v-slot:icon>
                    <ShareSvg class="adv-btn__icon"></ShareSvg>
                </template>
            </Btn>
        </template>
        <template v-slot:popover-content>
            <div class="adv-g__share-menu">
                <div v-if="!shortURL">
                    <Spinner class="adv-g__share-menu__spinner"></Spinner>
                </div>
                <template v-if="shortURL">
                    <div class="adv-g__share-menu__title">
                        {{i18n.offerInformation.share}}
                    </div>
                    <social-sharing v-bind:url="pdfUrl"
                                    v-bind:title="offer.title || offer.promotionTitle"
                                    v-bind:description="offer.mainDiscountString"
                                    v-bind:quote="offer.title || offer.promotionTitle || offer.mainDiscountString"
                                    inline-template>
                        <div class="adv-g__share-menu__list">
                            <network network="facebook">
                                <Btn type="link"
                                     class="adv-g__share-btn">
                                    <template v-slot:icon>
                                        <Facebook class="adv-btn__icon"></Facebook>
                                    </template>
                                </Btn>
                            </network>
                            <network network="telegram">
                                <Btn type="link"
                                     class="adv-g__share-btn">
                                    <template v-slot:icon>
                                        <Telegram class="adv-btn__icon"></Telegram>
                                    </template>
                                </Btn>
                            </network>
                            <network network="twitter">
                                <Btn type="link"
                                     class="adv-g__share-btn">
                                    <template v-slot:icon>
                                        <Twitter class="adv-btn__icon"></Twitter>
                                    </template>
                                </Btn>
                            </network>
                            <network network="whatsapp">
                                <Btn type="link"
                                     class="adv-g__share-btn">
                                    <template v-slot:icon>
                                        <Whatsapp class="adv-btn__icon"></Whatsapp>
                                    </template>
                                </Btn>
                            </network>
                            <network network="viber">
                                <Btn type="link"
                                     class="adv-g__share-btn">
                                    <template v-slot:icon>
                                        <Viber class="adv-btn__icon"></Viber>
                                    </template>
                                </Btn>
                            </network>
                        </div>
                    </social-sharing>
                </template>
            </div>
        </template>
    </popover>
</template>

<script>
    import Btn from "../../ui/Button";
    import Facebook from "../../../assets/facebook-app-logo.svg";
    import Twitter from "../../../assets/twitter-app-logo.svg";
    import Whatsapp from "../../../assets/whatsapp-app-logo.svg";
    import Telegram from "../../../assets/telegram-app-logo.svg";
    import Viber from "../../../assets/viber-app-logo.svg";
    import ShareSvg from "../../../assets/share.svg";
    import Vue from 'vue';
    import Popover from "../../ui/Popover";
    import { i18nAwareMixin } from "../../../mixins/i18n/I18nAware";
    import { DownloadFileMixin } from "../../../mixins/DownloadFile";
    import Spinner from '../../../assets/three-dots.svg';
    import { createShortShareURL } from "./share-helper";

    Vue.component('Btn', Btn);
    Vue.component('Facebook', Facebook);
    Vue.component('Twitter', Twitter);
    Vue.component('Whatsapp', Whatsapp);
    Vue.component('Telegram', Telegram);
    Vue.component('Viber', Viber);

    export default {
        name: "ShareBtn",
        mixins: [i18nAwareMixin, DownloadFileMixin],
        components: { Popover, Btn, ShareSvg, Spinner },
        props: {
            offer: {
                type: Object,
                required: true
            },
        },
        data() {
            return {
                shortURL: undefined,
                loadingShortURL: false,
            };
        },
        methods: {
            onShareBtnFirstOpen() {
                if (this.shortURL || this.loadingShortURL) {
                    return;
                }

                this.loadingShortURL = true;

                createShortShareURL(this.pdfUrl).then(shortURL => {
                    this.shortURL = shortURL;
                    this.loadingShortURL = false;
                });
            }
        }
    }
</script>

<style>
    .adv-g__share-menu {
        padding: 15px 15px 10px 15px;
    }

    .adv-g__share-menu__list {
        display: flex;
        margin: 10px 0;
    }

    .adv-g__share-menu__title {
        font-size: 0.9rem;
        text-overflow: ellipsis;
        white-space: nowrap;
        color: var(--adv-gallery-secondary-text-color);
    }

    .adv-g__share-menu__list .adv-g__share-btn {
        padding: 0;
        height: 24px;
        min-height: 24px;
    }

    .adv-g__share-menu__list > *:not(:last-child) {
        margin-right: 10px;
    }

    .adv-g__share-menu__spinner {
        width: 50px;
        height: 20px;
    }

    .adv-g__share-menu__spinner * {
        fill: var(--adv-gallery-secondary-text-color);
    }
</style>
