<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
    <g-signin-button
            v-if="auth.google.initialized"
            :params="googleSignInParams"
            @success="onSignInSuccess"
            @error="onSignInError">
        <Btn
                type="link" class="adv-g__login-form__google-login"
        >
            <template v-slot:icon>
                <GoogleSvg class="adv-btn__icon adv-g__login-form__google-login__icon"></GoogleSvg>
            </template>
        </Btn>
    </g-signin-button>
</template>

<script>
    import GoogleSvg from '../../../assets/btn_google_light_normal_ios.svg';
    import {AuthAwareMixin} from "../../../mixins/authentication/AuthenticationAware";

    export default {
        components: {GoogleSvg},
        mixins: [AuthAwareMixin],
        data() {
            return {
                scriptAdded: false
            }
        },
        methods: {
            onSignInSuccess(googleUser) {
                this.auth.onUserLoggedInWithGoogle(googleUser);
            },
            onSignInError() {}
        },
        computed: {
            googleSignInParams() {
                return {
                    client_id: this.auth.googleClientId
                };
            },
        }
    }
</script>

<style scoped>
    .adv-g__login-form__google-login {
        padding: 0;
    }

    .adv-g__login-form__google-login__icon {
        border: 1px solid var(--adv-gallery-secondary-border-color);
        border-radius: 4px;
    }

    .adv-g__login-form__google-login > .adv-btn__icon {
        width: 38px;
        height: 38px;
        padding: 0;
    }
</style>
