<template>
    <div class="adv-g__cart__item">
        <img
                class="adv-g__cart__item__image"
                v-bind:src="coupon.imageLink"
        >
        <div class="adv-g__cart__item__info">
            <div class="adv-g__cart__item-title">{{title}}</div>
            <div>
                <span class="adv-g__cart__item__valid-till-message">{{coupon.actionTimeString}}
                </span>&#32;<span class="adv-g__cart__item__valid-till-date">{{formattedDate}}</span>
            </div>
        </div>
        <Btn
                type="link"
                class="adv-g__cart__item__remove"
                v-on:click="$emit('remove', coupon)"
        >
            <template v-slot:icon>
                <CloseSvg class="adv-btn__icon"></CloseSvg>
            </template>
        </Btn>
    </div>
</template>

<script>
    import CloseSvg from '../../../assets/close.svg';
    import Btn from "../../ui/Button";

    export default {
        name: "cart-list-item",
        components: {
            Btn,
            CloseSvg,
        },
        props: {
            coupon: {
                type: Object,
            }
        },
        computed: {
            title() {
                return this.coupon.productTitles ? this.coupon.productTitles[0] : this.coupon.promotionTitle;
            },
            formattedDate() {
                return new Date(this.coupon.endDateTime).toLocaleDateString();
            }
        }
    }
</script>

