    }

    .adv-g__cart__item__info {
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding-left: 15px;
        width: calc(100% - 64px);
    }

    .adv-g__cart__item-title {
        font-weight: bold;
        font-size: 0.9rem;
        line-height: 1.2rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        width: 90%;
    }

    .adv-g__cart__item__remove {
        position: absolute;
        right: 10px;
        top: 10px;
        padding: 0;
    }

    .adv-g__cart__item__remove .adv-btn__icon {
        fill: var(--adv-gallery-secondary-text-color);
